"use client";

import { useState, useRef, useEffect } from "react";

export default function Dropdown({
  label,
  value,
  options,
  onChange,
}: {
  label: string;
  value: string;
  options: string[];
  onChange: (v: string) => void;
}) {
  const [open, setOpen] = useState(false);
  const ref = useRef<HTMLDivElement | null>(null);

  // Close when clicking outside
  useEffect(() => {
    const handle = (e: MouseEvent) => {
      if (ref.current && !ref.current.contains(e.target as Node)) {
        setOpen(false);
      }
    };
    document.addEventListener("click", handle);
    return () => document.removeEventListener("click", handle);
  }, []);

  return (
    <div className="flex-1" ref={ref}>
      {/* Label */}
      <label
        className="text-sm font-medium"
        style={{ color: "var(--color-text-secondary)" }}
      >
        {label}
      </label>

      {/* Select Button */}
      <div
        onClick={() => setOpen(!open)}
        className="mt-1 w-full px-3 py-2.5 rounded-2xl text-sm cursor-pointer flex items-center justify-between"
        style={{
          background: "var(--color-bg-neutral-50)",
          border: "1px solid var(--color-btn-teritary-200)",
          color: "var(--color-text-primary)",
        }}
      >
        {value}

        {/* Arrow icon */}
        <svg
          width="14"
          height="14"
          viewBox="0 0 24 24"
          fill="none"
          stroke="var(--color-text-secondary)"
          strokeWidth="2"
          strokeLinecap="round"
          strokeLinejoin="round"
          className={`transition-transform ${open ? "rotate-180" : ""}`}
        >
          <polyline points="6 9 12 15 18 9"></polyline>
        </svg>
      </div>

      {/* Dropdown Menu */}
      {open && (
        <div
          className="absolute mt-2 w-full rounded-2xl shadow-lg z-50 py-2"
          style={{
            background: "var(--color-bg-neutral-50)",
            border: "1px solid var(--color-btn-teritary-200)",
          }}
        >
          {options.map((opt) => (
            <div
              key={opt}
              onClick={() => {
                onChange(opt);
                setOpen(false);
              }}
              className="px-4 py-2 text-sm cursor-pointer rounded-xl"
              style={{
                color: "var(--color-text-secondary)",
              }}
            >
              {opt}
            </div>
          ))}
        </div>
      )}
    </div>
  );
}
